"use client";
import React, { useState, useEffect } from 'react';
import {
    ArrowLeft,
    FileCheck,
    CheckCircle2,
    ChevronRight,
    ShieldCheck,
    FileText
} from 'lucide-react';
import Link from 'next/link';
import '@/styles/pages/Business.css';

const formationSteps = [
    {
        id: 'step1',
        title: 'Entity Selection & Name Search',
        desc: "Determine if an LLC is the right structure and verify your business name availability with your state's Secretary of State database.",
        tasks: [
            "Decide on LLC vs. Corp (LLC recommended for most Builders)",
            "Perform name search on Secretary of State website",
            "Verify URL/Domain availability for the name"
        ]
    },
    {
        id: 'step2',
        title: 'File Articles of Organization',
        desc: "This is the birth certificate of your business. Filing this document officially creates your legal entity.",
        tasks: [
            "Locate specific filing portal for your State",
            "Pay the state filing fee (usually $50 - $200)",
            "Receive potential immediate digital confirmation or wait for mail"
        ]
    },
    {
        id: 'step3',
        title: 'Obtain EIN (Tax ID)',
        desc: "Your Employer Identification Number is required for banking. Do NOT use third-party services that charge for this; it is free from the IRS.",
        tasks: [
            "Visit IRS.gov EIN Assistant during business hours",
            "Select 'Limited Liability Company'",
            "Download and save the CP-575 confirmation letter immediately"
        ]
    },
    {
        id: 'step4',
        title: 'Operating Agreement & BOI',
        desc: "Internal governance documents and mandatory federal transparency reporting to FinCEN.",
        tasks: [
            "Draft Operating Agreement (defines ownership %)",
            "File Beneficial Ownership Information (BOI) report at fincen.gov",
            "Store all documents in a secure digital vault"
        ]
    }
];

export default function FormationPage() {
    const [completedTasks, setCompletedTasks] = useState<string[]>([]);

    useEffect(() => {
        const saved = localStorage.getItem('formation_progress');
        if (saved) {
            try {
                setCompletedTasks(JSON.parse(saved));
            } catch (e) {
                console.error("Failed to load progress", e);
            }
        }
    }, []);

    const toggleTask = (task: string) => {
        setCompletedTasks(prev => {
            const newSet = prev.includes(task)
                ? prev.filter(t => t !== task)
                : [...prev, task];

            localStorage.setItem('formation_progress', JSON.stringify(newSet));
            return newSet;
        });
    };

    const isStepComplete = (stepTasks: string[]) => {
        return stepTasks.every(t => completedTasks.includes(t));
    };

    const progressPercentage = Math.round((completedTasks.length / formationSteps.flatMap(s => s.tasks).length) * 100);

    return (
        <div className="business-detail-page animate-fade-in">
            {/* Header / Nav */}
            <div className="detail-header">
                <Link href="/dashboard/business" className="back-link">
                    <ArrowLeft size={20} />
                    <span>Back to Command Centre</span>
                </Link>
                <div className="progress-indicator">
                    <span className="label">Protocol Completion</span>
                    <div className="progress-track">
                        <div className="progress-fiill" style={{ width: `${progressPercentage}%` }}></div>
                    </div>
                    <span className="val">{progressPercentage}%</span>
                </div>
            </div>

            <div className="protocol-detail-container">
                {/* Immersive Command Hero */}
                <div className="protocol-command-hero glass-panel">
                    <div className="hero-visual-mesh-v2">
                        <div className="orbit"></div>
                        <div className="orbit"></div>
                    </div>

                    <div className="command-header-block">
                        <div className="protocol-status-badge">Phase 01: Active</div>
                        <h1>Formation <span className="text-accent">Protocol</span></h1>
                        <p className="hero-desc max-w-2xl">
                            The inaugural step in entity evolution. Constructing a robust legal chassis
                            to house your intellectual and financial capital while maintaining absolute
                            liability insulation.
                        </p>

                        <div className="protocol-stats-strip">
                            <div className="stat-item-premium">
                                <span className="label">Protection Level</span>
                                <span className="value text-accent">Maximum</span>
                            </div>
                            <div className="stat-item-premium">
                                <span className="label">Tax Status</span>
                                <span className="value">Pass-Through</span>
                            </div>
                            <div className="stat-item-premium">
                                <span className="label">Milestones</span>
                                <span className="value">04 Secure</span>
                            </div>
                        </div>
                    </div>

                    <div className="pro-tip-box">
                        <h5>Institutional Insight</h5>
                        <p>
                            Filing in your home state (Nexus) is the tactical default for most builders.
                            However, if you prioritize privacy and asset anonymity, Wyoming remains the
                            gold standard for high-net-worth deployment.
                        </p>
                    </div>
                </div>

                {/* Milestone Mesh Grid */}
                <div className="milestones-mesh">
                    {formationSteps.map((step, index) => {
                        const isComplete = isStepComplete(step.tasks);

                        return (
                            <div key={step.id} className={`milestone-mesh-card ${isComplete ? 'completed' : ''}`}>
                                <div className="milestone-header">
                                    <div className="id-badge">BP-{index + 1 < 10 ? `0${index + 1}` : index + 1}</div>
                                    <div className="milestone-info">
                                        <h3>{step.title}</h3>
                                        <p>{step.desc}</p>
                                    </div>
                                    {isComplete && <div className="ml-auto"><CheckCircle2 className="text-green-500" size={32} /></div>}
                                </div>

                                <div className="tactical-task-grid">
                                    {step.tasks.map((task, i) => (
                                        <div
                                            key={i}
                                            className={`tactical-task-item ${completedTasks.includes(task) ? 'complete' : ''}`}
                                            onClick={() => toggleTask(task)}
                                        >
                                            <div className="task-indicator">
                                                {completedTasks.includes(task) && <CheckCircle2 size={16} />}
                                            </div>
                                            <span className="task-text">{task}</span>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        );
                    })}

                    <div className="completion-action mt-20">
                        <button
                            className={`btn-complete-protocol ${progressPercentage === 100 ? 'active' : ''}`}
                            disabled={progressPercentage < 100}
                        >
                            <span className="btn-glow"></span>
                            {progressPercentage === 100 ? 'Formation Finalized - Initialize Banking Mesh' : 'Awaiting Task Completion'}
                            <ChevronRight size={20} />
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
